/*--------------------------------------------------------------
     animate style
  --------------------------------------------------------------*/
@keyframes thumb_loading {
  0% {
    -webkit-transform: scale(0.85);
    transform: scale(0.85)
  }

  100% {
    -webkit-transform: scale(1);
    transform: scale(1)
  }
}

@keyframes title_loading {
  0% {
    width: 80%
  }
  100% {
    width: 95%
  }
}

@keyframes content_loading {
  0% {
    width: 60%
  }
  100% {
    width: 80%
  }
}