  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
}

iframe {
  background-color: var(--background);
  vertical-align: middle;
}

a {
  color: var(--title);
  text-decoration: none;
  transition: all .3s ease;
  cursor: pointer;

  &:link,
  &:visited {
    text-decoration: none;
  }

  &:hover {
    text-decoration: none;
    color: var(--theme);
  }
}

img {
  max-width: 100%;

  &[data-src] {
    opacity: 0;
    transition: opacity .25s ease-in-out;
    will-change: opacity;

    &[src] {
      opacity: 1;
    }
  }

  &.owo_image {
    height: 22px !important;
  }
}

a img {
  border: 0 none;
}

fieldset {
  margin: 0;
  padding: 0;
}

button {
  overflow: visible;
  padding: 0;
  outline: none;
  transition: all .3s ease;
}

input {
  &.ie6 {
    overflow: visible;
    padding: 0;
    outline: none;
    transition: all .3s ease;
  }

  -webkit-appearance: none;
  outline: none !important;
  box-shadow: none !important;

  &:focus {
    -webkit-appearance: none;
    outline: none !important;
    box-shadow: none !important;
  }

  &[type="text"] {
    -webkit-appearance: none;
    outline: none !important;
    box-shadow: none !important;

    &:focus {
      -webkit-appearance: none;
      outline: none !important;
      box-shadow: none !important;
    }
  }
}

select,
textarea {
  -webkit-appearance: none;
  outline: none !important;
  box-shadow: none !important;
}

input {
  &[type=checkbox] {
    -webkit-appearance: checkbox !important;
  }

  &[type=radio] {
    -webkit-appearance: radio !important;
  }
}

button {
  outline: none !important;
  box-shadow: none !important;

  &:focus {
    outline: none !important;
    box-shadow: none !important;
  }
}

select,
textarea {
  height: 40px;
  border: 1px solid var(--block-border);
  width: 100%;
  font-size: 15px;
  padding: 0 16px;
  box-shadow: none;
  border-radius: 3px;
  transition: all 0.3s ease-in-out;
  resize: none;
}

input:focus,
textarea:focus {
  border-color: var(--textarea-border);
  background-color: var(--background);
}

.btn {

  &.disabled,
  &[disabled] {
    opacity: .9;
  }
}

fieldset[disabled] .btn {
  opacity: .9;
}

table {
  border-spacing: 0;
}

td,
th,
caption {
  padding: 0;
}

blockquote {
  position: relative;
  padding: 0 0 0 80px;
  margin: 40px 0;
  border: 0;

  p {
    line-height: 1.6;
    color: var(--text-dark);
  }

  &::before {
    content: "\201C";
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    width: 80px;
    height: 60px;
    font-size: 70px;
    line-height: 60px;
    color: #BFBFBF;
    font-family: sans-serif;
    text-align: center;
    z-index: 1;
  }

  cite,
  small {
    display: block;
    margin-top: 15px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    opacity: 0.7;
  }

  cite::before {
    position: relative;
    bottom: 3px;
    display: inline-block;
    width: 20px;
    height: 1px;
    margin-right: 10px;
    background: var(--text-dark);
    content: "";
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  blockquote {
    padding: 0 0 0 40px;

    &::before {
      width: 30px;
      height: 30px;
      font-size: 50px;
      line-height: 40px;
    }

    p {
      font-size: 14px !important;
      margin-bottom: 10px !important;
    }
  }
}

@media (max-width: 767px) {
  blockquote {
    padding: 0 0 0 40px;

    &::before {
      width: 30px;
      height: 30px;
      font-size: 50px;
      line-height: 40px;
    }

    p {
      font-size: 16px !important;
    }
  }
}

hr {
  margin: 3rem 0;
}

.hr-short::before {
  content: "";
  display: block;
  width: 110px;
  margin: 35px 0;
  height: 2px;
  background-color: var(--block-border);
}

table {
  border-collapse: collapse;
  margin: 0 0 15px;
  word-break: normal;
}

th,
td {
  border: 1px solid var(--block-border);
  padding: 8px 12px;
}

iframe,
embed {
  border: 0;
  display: block;
  overflow: hidden;
  width: 100%;
}

figure {
  margin: 0;
}

pre {
  overflow-y: auto;
  background-color: var(--block-background);
  border: 1px solid var(--block-border);
  border-radius: 0;
  margin: 20px 0;
  white-space: pre-wrap;

  code {
    padding: 15px;
    display: block;

    &.hljs {
      padding: 15px;

      ul {
        list-style: decimal;
        margin: 0 0 0 40px !important;
        padding: 0;

        li {
          list-style: decimal-leading-zero;
          border-left: 1px solid #ddd !important;
          padding: 5px !important;
          margin: 0 !important;
          white-space: pre-wrap;
        }
      }
    }
  }
}

svg.svg-inline--fa:not(:root) {
  vertical-align: -.125em;
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  transition: height .5s ease;
}

main {
  min-height: 66.66vh;
}

