/*--------------------------------------------------------------
      btn style
  --------------------------------------------------------------*/

.btn {
  border-radius: .22rem;
  white-space: normal;
  padding: .375rem 1rem;
  cursor: pointer;


  i, svg {
    margin-right: .5em;
  }


  &.qq {
    color: #3d93e0;

    svg {
      fill: #3d93e0;
    }
  }

  &.weibo {
    color: #e24040;

    svg {
      fill: #e24040;
    }
  }

  &.weixin {
    color: #20b767;

    svg {
      fill: #20b767;
    }
  }

  &.edit {
    color: var(--theme);

    svg {
      fill: var(--theme);
    }
  }

  &.text-free {
    svg {
      margin-right: 0;
    }
  }
}

.btn-shadow {
  box-shadow: 0 0 45px -5px rgba(158, 158, 158, 0.22) !important;
}

.btn-group-sm > .btn,
.btn-sm {
  font-size: .75rem;
  padding: 0.25rem 0.75rem;
}

.btn-primary {
  background-color: var(--theme);
  border-color: var(--theme);
  border-color: var(--theme);
  box-shadow: 0 5px 20px -3px rgba(249, 100, 90, 0.6) !important;
  color: var(--text-light);

  svg {
    fill: var(--text-light);
  }

  &:hover {
    color: var(--text-light);
    background-color: var(--title);
    border-color: var(--title);
    box-shadow: 0 5px 20px -3px rgba(37, 44, 65, 0.33) !important;
  }

  &:focus,
  &.focus {
    color: var(--text-light);
    background-color: var(--title);
    border-color: var(--title);
    box-shadow: none;
  }

  &:not(:disabled):not(.disabled) {

    &:active:focus,
    &.active:focus {
      color: var(--text-light);
      background-color: var(--title);
      border-color: var(--title);
      box-shadow: none;
    }
  }

  &.disabled,
  &:disabled {
    color: var(--text-light);
    background-color: var(--theme);
    border-color: var(--theme);
  }

  &:not(:disabled):not(.disabled) {

    &:active,
    &.active {
      color: var(--text-light);
      background-color: var(--theme);
      border-color: var(--theme);
    }
  }
}

.show > .btn-primary.dropdown-toggle:focus {
  color: var(--text-light);
  background-color: var(--title);
  border-color: var(--title);
  box-shadow: none;
}

.show > .btn-primary.dropdown-toggle {
  color: var(--text-light);
  background-color: var(--theme);
  border-color: var(--theme);
}

.btn-secondary {
  background-color: #252c41;
