    color: var(--text-light);
    background-color: var(--title);
    border-color: var(--title);
    box-shadow: 0 5px 20px -3px rgba(37, 44, 65, 0.33) !important;
  }

  &:not(:disabled):not(.disabled) {

    &:active:focus,
    &.active:focus {
      color: var(--text-light);
      background-color: var(--title);
      border-color: var(--title);
      box-shadow: 0 5px 20px -3px rgba(37, 44, 65, 0.33) !important;
    }
  }
}

.show > .btn-secondary.dropdown-toggle:focus {
  color: var(--text-light);
  background-color: var(--title);
  border-color: var(--title);
  box-shadow: 0 5px 20px -3px rgba(37, 44, 65, 0.33) !important;
}

.btn-secondary {

  &.disabled,
  &:disabled {
    color: #f4f5f9;
    background-color: var(--title);
    border-color: var(--title);
  }

  &:not(:disabled):not(.disabled) {

    &:active,
