}

.btn-light {
  color: #252c41;
  background-color: #f4f5f9;
  border-color: #f4f5f9;

  svg {
    fill: #252c41;
  }

  &:hover {
    color: var(--text-light);
    background-color: var(--title);
    border-color: var(--title);
    box-shadow: 0 5px 20px -3px rgba(37, 44, 65, 0.33);
    transition: all .146s;

    svg {
      fill: var(--text-light);
    }
  }

  &:focus,
  &.focus {
    color: #9d9fa9;
    background-color: #f4f5f9;
    border-color: #f4f5f9;
    box-shadow: none;

    svg {
      fill: #9d9fa9;
    }
  }

  &:not(:disabled):not(.disabled) {

    &:active:focus,
    &.active:focus {
      color: #9d9fa9;
      background-color: #f4f5f9;
      border-color: #f4f5f9;
      box-shadow: none;

      svg {
        fill: #9d9fa9;
      }
    }
  }
}

.show > .btn-light.dropdown-toggle:focus {
  color: #9d9fa9;
  background-color: #f4f5f9;
  border-color: #f4f5f9;
  box-shadow: none;
}

.btn-light {

  &.disabled,
  &:disabled {
    color: #9d9fa9;
    background-color: #f1eff3;
    border-color: #f1eff3;
  }

  &:not(:disabled):not(.disabled) {

    &:active,
    &.active {
      color: #9d9fa9;
      background-color: #f4f5f9;
      border-color: #f4f5f9;
    }
  }
}

.show > .btn-light.dropdown-toggle {
  color: #9d9fa9;
  background-color: #f4f5f9;
  border-color: #f4f5f9;
}

.btn-outline-light {
  color: #252c41;
  background-color: var(--background);
  background-image: none;
  border-color: #f4f5f9;

  &:hover {
    color: var(--title);
    background-color: var(--background);
    border-color: #f4f5f9;
  }

  &:focus,
  &.focus {
    box-shadow: 0 0 0 0.2rem rgba(248, 249, 250, 0.5);
  }

  &.disabled,
  &:disabled {
    color: #f8f9fa;
    background-color: #f4f5f9;
  }

  &:not(:disabled):not(.disabled) {

    &:active,
    &.active {
      color: #9d9fa9;
      background-color: var(--background);
      border-color: #f4f5f9;
    }
  }
}

.show > .btn-outline-light.dropdown-toggle {
  color: #9d9fa9;
  background-color: var(--background);
  border-color: #f4f5f9;
}

.btn-outline-light:not(:disabled):not(.disabled) {

  &:active:focus,
  &.active:focus {
    box-shadow: 0 0 0 0.2rem rgba(248, 249, 250, 0.5);
  }
}

.show > .btn-outline-light.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba(248, 249, 250, 0.5);
}

.btn-outline-secondary {
  color: #252c41;
  background-color: transparent;
  background-image: none;
  border-color: #252c41;

  &:hover {
    color: var(--text-light);
    background-color: #252c41;
    border-color: #252c41;
  }

  &:focus,
  &.focus {
    box-shadow: none;
    color: var(--text-light);
    background-color: #252c41;
    border-color: #252c41;
  }

  &.disabled,
  &:disabled {
    color: #252c41;
    background-color: transparent;
  }

  &:not(:disabled):not(.disabled) {

    &:active,
    &.active {
      color: var(--text-light);
      background-color: #dddfe6;
      border-color: #dddfe6;
    }
  }
}

.show > .btn-outline-secondary.dropdown-toggle {
  color: var(--text-light);
  background-color: #dddfe6;
  border-color: #dddfe6;
}

.btn-outline-secondary:not(:disabled):not(.disabled) {

  &:active:focus,
  &.active:focus {
    box-shadow: none;
  }
}

.show > .btn-outline-secondary.dropdown-toggle:focus {
  box-shadow: none;
}

.btn-rounded {
  border-radius: 4rem;
  padding-left: 1rem;
  padding-right: 1rem;
}

/*--------------------------------------------------------------
      rounded style
  --------------------------------------------------------------*/

.rounded {
  border-radius: 0.33rem;
  overflow: hidden;
}

.rounded-circle {
  overflow: hidden;
}

/*--------------------------------------------------------------
      title Font Size style
  --------------------------------------------------------------*/

.bg-dark {
  background-color: var(--title) !important;
}

.bg-secondary {
  background-color: #252c41 !important;
}

.bg-light {
  background-color: #f4f5f9 !important;
}

.bg-white-overlay {
  color: var(--text-light);
  background-color: rgba(255, 255, 255, 0.35);
}

.bg-dark-overlay {
  color: var(--text-light);
  background-color: rgba(0, 0, 0, 0.33);
}

.bg-overlay {
  position: fixed;
  height: 100%;
  right: -99%;
  top: 0;
  left: -99%;
  opacity: 0;
  background: rgba(120, 129, 147, 0.22);
  visibility: hidden;
  transform: translate3d(0px, 0, 0);
  transition: 0.3s ease-in-out;
  z-index: 998;
}

/*--------------------------------------------------------------
      title Font Size style
  --------------------------------------------------------------*/

.shadow {
  box-shadow: 0 10px 30px -10px rgba(45, 55, 86, 0.2) !important;
}

.shadow-2x {
  box-shadow: 0 10px 50px -15px rgba(0, 0, 0, 0.1) !important;
}

.no-shadow {
  box-shadow: none !important;
}

@media (max-width: 767.98px) {
  .shadow {
    box-shadow: 0 10px 15px -2px rgba(45, 55, 86, 0.1) !important;
  }

  .shadow-2x {
    box-shadow: 0 5px 20px -10px rgba(0, 0, 0, 0.1) !important;
  }
}

/*--------------------------------------------------------------
      badge style
  --------------------------------------------------------------*/

.badge {
  border-radius: .15rem;
  font-weight: normal;
}

.badge-sm {
  padding: 0.25rem 0.35rem;
}

.badge-md {
  padding: 0.5rem 0.75rem;
}

.badge-outline-primary {
  color: var(--theme);
  border: var(--theme) solid 1px;
}

.badge-light {
  color: #252c41;
  background-color: #f4f5f9;

  &[href] {

    &:hover,
    &:focus {
      color: var(--title);
      text-decoration: none;
      background-color: #f4f5f9;
    }
  }
}

/*--------------------------------------------------------------
      font-line
  --------------------------------------------------------------*/

.x-font-line {
  position: relative;
  box-shadow: inset 0 -4px 0 var(--theme);
}

.x-m-line,
.x-b-line {
  position: relative;
}

.x-m-line:before {
  content: '';
  left: 1px;
  right: 1px;
  height: 22%;
  background-color: var(--theme);
  position: absolute;
  opacity: 0.33;
  top: 50%;
  transform: translate(0%, -50%);
}

.x-b-line:before {
  content: '';
  height: 22%;
  left: 1px;
  right: 1px;
  background-color: var(--theme);
  position: absolute;
  opacity: 0.6;
  bottom: 0;
}

.text-line-center {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
}

/*--------------------------------------------------------------
      title Font Size style
  --------------------------------------------------------------*/

.border,
.border-bottom,
.border-left,
.border-right,
.border-top {
  border-color: #f4f5f9;
}

.border-primary {
  border-color: var(--theme) !important;
}

/*--------------------------------------------------------------
      form-control style
  --------------------------------------------------------------*/
.input-group {
  .form-control-plaintext {
    flex-grow: 1;
    width: auto;
    padding-left: .5em;
    padding-right: .5em;
  }
}

.form-control {
  color: #707680;
  border-radius: .22rem;
  background-color: #f7f9fb;

  &.secret {
    background: repeating-linear-gradient(145deg, #f2f2f2, #f2f2f2 15px, #fff 0, #fff 30px);
  }

  &:focus {
    color: var(--title);
    border-color: var(--theme);
    background-color: #fff;
    box-shadow: none;
  }

  &::-webkit-input-placeholder,
  &::-moz-placeholder,
  &:-ms-input-placeholder,
  &::-ms-input-placeholder,
  &::placeholder {
    color: #707680;
    opacity: 1;
  }

  &:disabled,
  &[readonly] {
    background-color: #f4f5f9;
    opacity: 1;
    color: #dddfe6;
  }
}

/*--------------------------------------------------------------
      avatar style
  --------------------------------------------------------------*/

.avatar {
  position: relative;
  line-height: 1;
  border-radius: 500px;
  white-space: nowrap;
  font-weight: bold;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;

  img {
    border-radius: inherit;
    width: 100%;
  }
}

/*--------------------------------------------------------------
      media style
  --------------------------------------------------------------*/

.media {
  position: relative;
  display: block;
  overflow: hidden;
  padding: 0;
  flex-shrink: 0;
  border-radius: 0.33rem;

  &:after {
    content: '';
    display: block;
    padding-top: 100%;
  }

  &:not(:first-child):not(:last-child):not(:only-child) {
    border-radius: 0;
  }
}

.media-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 1rem 1rem;
  z-index: 2;
  display: flex;
  align-items: center;
  color: var(--text-light);

  &.overlay-top {
    bottom: auto;
  }

  &.overlay-bottom {
    top: auto;
  }
}

.media-content {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  border: 0;
  border-radius: inherit;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-color: rgba(120, 120, 120, 0.1);

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}

/*--------------------------------------------------------------
      list style
  --------------------------------------------------------------*/

.list {
  padding-left: 0;
  padding-right: 0;
}

.list-item {
  position: relative;
  display: flex;
  flex-direction: column;
  min-width: 0;
  word-wrap: break-word;

  &.loading {
    .list-title {
      background-color: rgba(120, 120, 120, .1);
      animation: title_loading 0.5s infinite alternate;
    }

    .list-image {
      animation: thumb_loading 0.5s infinite alternate;
    }

    .list-footer,
    .list-description {
      background-color: rgba(120, 120, 120, .1);
      animation: content_loading 0.5s infinite alternate;
    }
  }
}

.list-content {
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  justify-content: center;
}

.list-body {
  flex: 1 1 auto;
}

.list-footer {
  margin-top: 0.75rem;
}

.list-title {
  display: block;
  color: inherit;
}

/*--------------------------------------------------------------
      list row style
  --------------------------------------------------------------*/

.list-row .list-item {
  flex-direction: row;
  align-items: center;
  padding: 0.75rem 0.625rem;

  > * {
    padding-left: 0.625rem;
    padding-right: 0.625rem;
  }

  border-top: 1px solid rgba(135, 150, 165, 0.075);
  margin-top: -1px;
  margin-bottom: 0;
}

.list-bordered .list-item {
  border-top: 1px solid rgba(135, 150, 165, 0.075);
  margin-top: -1px;
  margin-bottom: 0;
}

.list-row .list-item:first-child,
.list-bordered .list-item:first-child {
  border-top: none;
}

/*--------------------------------------------------------------
      list grid style
  --------------------------------------------------------------*/

.list-grid {
  .list-item {
    flex-direction: row;

    .list-image {
      &:first-child {
        padding-right: 1rem;
        padding-left: 0;
      }

      &:last-child {
        padding-right: 0;
        padding-left: 1rem;
      }
    }

    .list-images .media {
      margin: 0;
    }
  }

  &.list-grid-padding {
    padding: .75rem 0;

    .list-item {
      padding: .5rem 1rem;

      .media {
        border-radius: inherit !important;
        margin-right: 0;
      }
    }

    .list-content {
      padding: 0 0 0 1rem;
    }
  }

  .flex-row-reverse .media:first-child {
    border-radius: 0;
    border-top-right-radius: inherit;
    border-bottom-right-radius: inherit;
  }
}

@media (max-width: 767.98px) {
  .list-grid .list-item .list-image {
    &:first-child {
      padding-right: .5rem;
      padding-left: 0;
    }

    &:last-child {
      padding-right: 0;
      padding-left: .5rem;
    }
  }
}

/*--------------------------------------------------------------
      overlay style
  --------------------------------------------------------------*/

.overlay {
  width: 100%;
  height: 100%;
  background-color: var(--overlay-background);
  position: absolute;
  opacity: .33;
  top: 0;
  left: 0;
  transition: opacity 0.3s ease-in-out;
}

.overlay-hover:hover .overlay,
a:hover .overlay {
  opacity: 0.22;
}

/*--------------------------------------------------------------
      line-clamp style
  --------------------------------------------------------------*/

.h-1x {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

.h-2x {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.h-3x {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.h-4x {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

/*--------------------------------------------------------------
      data-null style
  --------------------------------------------------------------*/

.data-null {
  text-align: center;

  h1 {
    font-size: 8rem;
    padding: 0;
  }

  p i {
    position: relative;
    display: inline-block;
    width: 200px;
    height: 300px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.tips-nothing {
  display: block;
  max-width: 300px;
  width: 100%;
  margin: 0 auto;
  transition: 0.5s;
}

/*--------------------------------------------------------------
     header style
  --------------------------------------------------------------*/

.header {
  z-index: 10;
  box-shadow: 0 0 20px 0 rgba(229, 232, 236, 0.5);
  background: #fff;

  &.fixed {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 0.3s;
    transform: translateY(0px);

    &.scroll .breadcrumbs {
      padding-bottom: .5rem;
    }
  }
}

.navbar-brand {

  span,
  img {
    height: 36px;
    width: auto;
  }
}

.nav-right {
  flex-wrap: nowrap;

  .action-button {
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
  }
}

/*--------------------------------------------------------------
      main-menu style
  --------------------------------------------------------------*/

.main-menu {
  opacity: 1;
  transition: all .3s ease;

  &.resize {
    opacity: 0;
    overflow: hidden;
  }

  li {
    position: relative;
    flex-shrink: 0;

    a {
      position: relative;
      display: flex;
      margin: 0 1.5rem 0 0;
      align-items: center;

      svg {
        width: 16px;
        height: 16px;
        fill: var(--theme);
      }
    }

    &:last-child a {
      margin: 0;
    }

    &:hover > a {
      color: var(--menu-hover);
    }
  }

  > li {
    animation: bounceInRight;
    animation-duration: .3s;
  }

  > li.current-menu-item > a:before {
    content: '';
    height: 4px;
    left: 1px;
    right: 1px;
    background-color: rgba(241, 64, 75, 0.33);
    position: absolute;
    top: 50%;
    transform: translate(0%, -50%);
  }

  > li.menu-item-has-children > a:after {
    top: -15px;
    right: -10px;
  }

  .sub-menu > li.menu-item-has-children > a:after {
    right: .5rem;
    top: 50%;
    transition: all .3s ease;
    transform: translate(-50%, -50%);
  }

  li ul.sub-menu {
    background: #ffffff none repeat scroll 0 0;
    left: 0;
    opacity: 0;
    position: absolute;
    top: 100%;
    transition: all 0.5s ease 0s;
    visibility: hidden;
    width: 180px;
    border: 1px solid #f4f5f9;
    box-shadow: 0 10px 50px -15px rgba(0, 0, 0, 0.1) !important;

    &:after {
      border: 8px solid transparent;
      border-bottom-color: #fff;
      content: "";
      left: 25px;
      margin-left: -10px;
      position: absolute;
      top: -15px;
      width: 16px;
      height: 16px;
    }

    &:before {
      border: 8px solid transparent;
      border-bottom-color: #f4f4f5;
      content: "";
      left: 25px;
      margin-left: -10px;
      position: absolute;
      top: -16px;
      width: 16px;
      height: 16px;
    }
  }

  .sub-menu > li ul.sub-menu {
    &:after {
      left: 2px;
      top: 50%;
      border: 8px solid transparent;
      border-right-color: #fff;
      transform: translate(-50%, -50%);
    }

    &:before {
      left: 1px;
      top: 50%;
      border: 8px solid transparent;
      border-right-color: #e6e6e8;
      transform: translate(-50%, -50%);
    }
  }

  li:hover > ul.sub-menu {
    opacity: 1;
    visibility: visible;
    margin-top: 15px;
    z-index: 11;
  }

  .sub-menu > li:hover ul.sub-menu {
    margin-top: 0;
  }

  li ul.sub-menu li {
    float: none;
    position: relative;
    border-bottom: 1px solid #f5f5f5;

    &:last-child {
      border: 0;
    }

    a {
      color: #152035;
      font-size: .875rem;
      padding: 1rem;
      margin: 0;
      letter-spacing: 1px;
    }

    &:hover > a {
      color: var(--menu-hover);
    }

    a i {
      float: right;
    }

    &.current-menu-item a,
    &.current-post-ancestor a {
      color: var(--theme);
    }

    ul {
      top: 0;
      left: 100%;
      opacity: 0;
      visibility: hidden;
    }
  }
}

@keyframes bounceInRight {

  from,
  60%,
  75%,
  90%,
  to {
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  from {
    opacity: 0;
    transform: translate3d(3000px, 0, 0) scaleX(3);
  }

  60% {
    opacity: 1;
    transform: translate3d(-25px, 0, 0) scaleX(1);
  }

  75% {
    transform: translate3d(10px, 0, 0) scaleX(0.98);
  }

  90% {
    transform: translate3d(-5px, 0, 0) scaleX(0.995);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.sign-menu {
  white-space: nowrap;

  .sub-menu {
    background: #ffffff none repeat scroll 0 0;
    right: 0;
    opacity: 0;
    position: absolute;
    transition: all 0.5s ease 0s;
    visibility: hidden;
    width: 180px;
    border: 1px solid #f4f5f9;
    padding: 5px 15px;
    box-shadow: 0 10px 50px -15px rgba(0, 0, 0, 0.1) !important;

    &:before {
      border: 8px solid transparent;
      border-bottom-color: #fff;
      content: "";
      position: absolute;
      top: -16px;
      width: 16px;
      height: 16px;
      right: 7px;
    }

    li {
      float: none;
      position: relative;
      border-bottom: 1px solid #f5f5f5;

      &:last-child {
        border: 0;
      }

      a {
        display: block;
        color: #152035;
        font-size: .875rem;
        padding: 1rem;
        margin: 0;
        letter-spacing: 1px;

        &:hover {
          color: var(--menu-hover);
        }
      }
    }
  }

  &:hover {
    > ul.sub-menu {
      opacity: 1;
      visibility: visible;
      margin-top: 12px;
      z-index: 11;
    }
  }

  .avatar {
    img {
      width: 30px;
      height: 30px;
      border-radius: 50%;
      border: 1px solid var(--block-border);
      padding: 3px;
      transition: transform 0.35s;
    }
  }

  &:hover {
    color: var(--menu-hover);
  }
}

@media (max-width: 991.98px) {
  .main-menu li {
    margin: 0.5rem 0;

    a {
      margin: 0;
    }

    &.current-menu-item a {
      color: var(--theme);

      &:before {
        display: none;
      }
    }
  }
}

@media (max-width: 767.98px) {
  .main-menu li.menu-item-has-children > a:after {
    right: 0;
    top: 50%;
    transform: translate(0%, -50%);
  }
}

.mobile-menu {
  position: fixed;
  width: 100%;
  max-width: 333px;
  height: 100vh;
  top: 0;
  left: -100%;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 3rem;
  background-color: var(--background);
  list-style: none;
  transition: all 0.3s ease 0s;
  box-shadow: 0 5px 65px -5px var(--popup-collapse-shadow-color);
  -webkit-overflow-scrolling: touch;
  z-index: 999;
}

.mobile-navbar.active .mobile-menu {
  left: 0;
}

.mobile-menu .navbar-nav {
  li {
    position: relative;
    display: block;
    transition: .2s;
  }

  .sub-menu {
    display: none;
    position: relative;

    li {
      padding: .25rem 0 .25rem 1rem;
    }
  }

  li a {
    font-size: 1rem;
    display: flex;
    margin: .75rem 0;
    letter-spacing: 1px;
    transition: .2s;
    align-items: center;
    justify-content: space-between;

    svg {
      width: 16px;
      height: 16px;
      fill: var(--theme);
    }
  }

  .sub-menu {
    li {
      &:last-child a {
        margin: 0.25rem 0;
      }

      &.current-menu-item {
        position: relative;

        a {
          color: var(--theme);
        }
      }
    }

    > li {

      .sub-menu > li:hover > a,
      &:hover > a {
        color: var(--theme);
      }
    }
  }

  > li {
    > ul > li {

      > ul > li:hover > a,
      &:hover > a {
        color: var(--theme);
      }
    }

    &:hover > a {
      color: var(--theme);
    }
  }

  li .menu-icon {
