/*--------------------------------------------------------------
      pushes style
  --------------------------------------------------------------*/

.pushes-list .item {
  .title {
    position: absolute;
    display: flex;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    z-index: 1;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    color: var(--text-light);

    span {
      opacity: 0;
      transition: transform .4s ease;
      transform: translate3d(0, 100%, 0);
    }
  }

  a:hover .title span {
    transition: transform .4s ease;
    transform: translate3d(0, 0, 0);
  }

  .media-hover {
    content: '';
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(241, 64, 75, 0.66);
    position: absolute;
    transition: transform 0.4s cubic-bezier(0.51, 0.01, 0.18, 1.03), opacity 0.3s ease;
    opacity: 0;
  }

  a:hover {

    .media-hover,
    .title span {
      opacity: 1;
    }
  }
}

/*--------------------------------------------------------------
