}

/*--------------------------------------------------------------
      post aside style
  --------------------------------------------------------------*/
.post-aside {
  position: absolute;
  height: 100%;
  width: 145px;
  right: 0;
  margin-right: -155px;
  opacity: 1;
  transition: opacity .2s ease-in;

  .x-catalog {
    background: #f4f5f9;
    padding-left: 30px !important;

    .catalog-item {
      position: relative;
      margin-bottom: 10px;
      color: #8e8787;
      font-weight: 600;

      &:last-child {
        margin-bottom: 0;
      }

      .catalog-item {
        margin-top: 10px;
        margin-left: 5px;
      }

      a {
        display: block;
        font-size: 13px;
        color: #e5e5e5;
        transition: color .5s;

        &.current {
          &:before {
            background: var(--theme) !important;
          }
        }

        &:before {
          background-color: #efecf4;
          content: '';
          position: absolute;
          width: 12px;
          height: 4px;
          border-radius: 20px;
          display: inline-block;
          vertical-align: middle;
          cursor: pointer;
          transition: background .5s;
          top: 9px;
          left: -20px;
          opacity: 1 !important;
        }
      }
    }
  }
}

@media (min-width: 1180px) {
  .post-aside {
    width: 160px;
    margin-right: -170px;
  }
}

@media (min-width: 1366px) {
