    width: 200px;
    margin-right: -220px;
  }
}

.post-social {
  position: absolute;
  height: 100%;
  opacity: 1;
  margin-left: -60px;
  transition: opacity .2s ease-in;
  left: 0;
  width: 40px;

  li {
    position: relative;
    text-align: center;
    display: block;
    font-size: 1.25rem;

    a {
      display: flex;
      justify-content: center;
      align-items: center;

      svg {
        width: 16px;
        height: 16px;
      }
    }
  }
}

@media (min-width: 768px) and (max-width: 991.98px) {
  .post-social {
    margin-left: -45px;
  }
}

