/*--------------------------------------------------------------
      sidebar style
  --------------------------------------------------------------*/

.search_overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  z-index: 106;
  opacity: 0.6;
  display: none;
}

.sidebar-collapse {
  .sidebar {
    position: fixed;
    width: 100%;
    max-width: 380px;
    height: 100vh;
    top: 0;
    right: -100%;
    overflow-x: hidden;
    overflow-y: auto;
    margin: 0;
    background-color: var(--background);
    list-style: none;
    transition: all 0.3s ease 0s;
    box-shadow: 0 5px 65px -5px var(--popup-collapse-shadow-color);
    -webkit-overflow-scrolling: touch;
    z-index: 999;
  }

  &.active .sidebar {
    right: 0;
  }
}

@media (max-width: 768px) {
  .sidebar-collapse .sidebar {
    width: 80%;
    max-width: 100%;
  }
}

body {

  &.active,
  &.active-sidebar {
    overflow: hidden;
  }

  &.active .bg-overlay,
  &.active-sidebar .bg-overlay {
    filter: brightness(0.2);
    cursor: pointer;
    opacity: 1;
    visibility: visible;
  }
}

/*--------------------------------------------------------------
      footer style
  --------------------------------------------------------------*/

.footer .bg-secondary {
  color: #e8e9ea;

  a {
    color: #e8e9ea;

    &:hover {
      color: var(--text-light);
    }
  }
}

/*--------------------------------------------------------------
      pagination
  --------------------------------------------------------------*/

.pagination {
  padding: 3rem 0 0;
}

.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;

  &:focus {
    background-color: var(--block-border);
    clip: auto !important;
    clip-path: none;
    color: #444;
    display: block;
    font-size: 1em;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
  }
}

.nav-links {
  position: relative;
  display: flex;
  width: 100%;

  > {

    span,
    a {
      position: relative;
      display: inline-block;
      background-color: #f4f5f9;
      padding: .5rem .875rem;
      margin: 0 .5rem 0 0;
      font-size: 0.75rem;
      border-radius: 0.2rem;
    }

    .prev:hover,
    .next:hover,
    .current {
      z-index: 2;
      color: #fff !important;
      background-color: var(--theme);
      box-shadow: 0 5px 20px -3px rgba(249, 100, 90, 0.6);
    }
  }

  .prev,
  .next {
    padding: .5rem .65rem;
    margin: 0;
  }

  .prev {
    margin-left: auto;
    margin-right: 10px;
  }

  .next {
    margin: 0 0 0 auto;
    right: 0;
  }

  .prev + .next {
    margin-left: 0;
  }
}

/*--------------------------------------------------------------
      back top top
  --------------------------------------------------------------*/

#x-back-to-top {
  position: fixed;
  width: 48px;
  height: 48px;
  right: 25px;
  bottom: 25px;
  z-index: 10000;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  .icon-stack {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    font-size: 1rem;
    text-align: center;
    color: var(--text-light);
    background-color: #252c41;
    overflow: hidden;
    transition: color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out;
    border-radius: 0.2rem;

    &:hover {
      background-color: var(--theme);
    }

    svg {
      position: absolute;
      top: 9px;
      left: 50%;
      transform: translateX(-50%);
      transition: .15s ease 50ms;
      fill: var(--text-light);
    }

    .back-to-top-text {
      display: inline-block;
      transform: translateY(5px);
      font-size: 11px;
      text-transform: uppercase;
      letter-spacing: .05em;
      line-height: 1em;
      transition: transform 0.35s cubic-bezier(0.165, 0.84, 0.44, 1);
    }

    &:hover {
      i, svg {
        opacity: 0;
        transform: translateX(-50%) translateY(-10px);
        transition: all 0.45s cubic-bezier(0.165, 0.84, 0.44, 1);
      }

      .back-to-top-text {
        transform: translateY(0);
        transition: transform 0.25s cubic-bezier(0.165, 0.84, 0.44, 1) 50ms;
      }
    }
  }
}

