/*--------------------------------------------------------------
      emoji  style
  --------------------------------------------------------------*/
@import "~owo/src/OwO";
.OwO {
  position: absolute;
  width: 100%;

  .OwO-logo {
    color: inherit;
    font-size: inherit;

    svg {
      width: 20px;
    }
  }

  &:not(.OwO-open) {
    .OwO-logo {
      border: 0;
    }
  }

  .OwO-body {
    .OwO-items {
      overflow-x: hidden;
    }

    .OwO-items-image {
      .OwO-item {
        width: calc(100% / 12 - 10px);
        margin: 5px;
      }
    }

    .OwO-bar {
      .OwO-packages {
        .OwO-package-active {
          color: #fff;
          background-color: var(--theme);

          &:hover {
            color: #fff;
            background-color: var(--theme) !important;
          }
        }
      }
    }
  }
}

/*--------------------------------------------------------------
      tabs  style
  --------------------------------------------------------------*/
.x-tabs {
  display: flex;
  flex-wrap: wrap;

  label {
    background-color: #d1d5db;
    cursor: pointer;
    display: block;
    font-weight: 700;
    order: 1;
    padding: 1rem 2rem;
    transition: background .2s ease;
    position: relative;
    z-index: 2;
    border-radius: 10px;

    &:before {
      content: "";
      background-color: transparent;
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 1;
      border: .5em solid #d1d5db;
    }
  }

  &.tab-on-bottom {
    label {
      order: 99
    }
  }

  .x-tab {
    order: 98;
    flex-grow: 1;
    width: 100%;
    display: none;
    padding: 1rem;
    background: #fff;
  }

  input[type="radio"] {
    display: none;

    &:checked {
      & + label {
        background: #fff;

        & + .x-tab {
          display: block;
        }
      }
    }
  }
}

@media (max-width: 767.96px) {
  .x-tabs {
    label {
      padding: .5em 1em;
    }
  }
}

@import "~@paper-development/vtoast/vtoast.css";
@import "animation";
@import "photos";